.page_lk {
	.header__top__right {
		.button_yellow_border {
			display: none;
		}
	}


}

.wrap_lk {

	> .container {
		position: relative;
		display: flex;
		flex-direction: column;
	}

	&__header {
		position: relative;
		padding-top: rem(29);
		padding-bottom: rem(8);
	
		background-color: #242336;

		@include min540 {
			padding-top: rem(30);
			padding-bottom: rem(30);
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: -100%;
			width: 9999em;
			height: 100%;
			background-color: #242336;
			z-index: -1;
			
		}

		@include min {
			order: 2;
			// position: static;
		}

		.container {
			display: flex;
			flex-direction: column;
		}

		.top {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;


		}
		
		.bottom {
			margin-top: rem(45);
			min-width: 0;

			@include min540 {
				margin-left: -15px;
				margin-right: -15px;
			}

			.swiper-slide {
				width: rem(60);
			}

			.item {
				width: rem(60);
				height: rem(60);
				border-radius: 50%;
				overflow: hidden;
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: var(--c-blue-accent);
			}

			&__scroll {
				width: 100%;
				height: rem(6);
				margin-top: rem(20);

				@include min {
					display: none;
				}

				.swiper-scrollbar-drag {
					background-color: #444263 !important;
					border-radius: rem(7) !important;
				}
			}

			&__fraction {
				display: none;

				@include min {
					display: flex;
					justify-content: center;
					align-items: flex-end;
					margin-top: rem(9);
					// margin-bottom: rem(24);
					color: white;

					span {
						color: white;
						font-weight: 500;
					}

					.swiper-pagination-current {
						font-size: rem(17);
						margin-right: rem(3);
					}

					.swiper-pagination-total {
						font-size: rem(15);
						margin-left: rem(3);
					}
				}
			}
		}
	}

	.button_wrap {
		margin-top: rem(20);
		margin-left: rem(75);
		margin-right: rem(75);

		margin-bottom: rem(100);

		display: flex;
		flex-wrap: wrap;

		@include min1200 {
			margin-top: rem(70);
			margin-bottom: rem(70);
			margin-left: 0;
			margin-right: 0;
		}

		@include min {
			order: 1;
			margin-top: 0;
			margin-bottom: rem(200);
		}

		.item {
			// flex: 1;
			height: rem(250);
			background-color: #242336;

			margin-right: rem(5);
			margin-bottom: rem(5);

			display: flex;
			align-items: center;
			justify-content: center;

			flex: 0 0 calc((100% - 5px) / 2);

			@include min {
				flex: 0 0 100%;
				height: rem(54);
				margin-bottom: rem(25);
				justify-content: flex-start;
				padding: 0 rem(16);

				&:last-child {
					margin-bottom: 0;
				}
			}

			&:hover {
				background-color: var(--c-bg);
			}
			

			&:nth-child(2n) {
				margin-right: 0;
			}
			
			.icon {
				margin-right: rem(28);

				@include min {
					margin-right: rem(40);

					svg {
						width: rem(20) !important;
					}
				}
			}

			.text {
				font-size: rem(20);
				color: var(--c-text-gray-light);

				@include min {
					font-size: rem(15);
				}
			}

			.count {
				font-size: rem(15);
				font-weight: 500;
				color: var(--c-yellow);
				margin-left: rem(10);
			}

		}
	}

	.lk_menu {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: rem(50);

		@include min {
			order: 3;
			margin-top: rem(80);
		}

		@include min540 {
			justify-content: center;
			flex-wrap: wrap;
		}

		&__item {

			display: flex;
			align-items: center;

			margin-right: rem(70);

			&:last-child {
				margin-right: 0;
			}

			&:nth-child(2) {
				margin-right: auto;
			}

			&:hover {

				svg {
					path {
						fill: var(--c-yellow);
					}
				}

				span {
					color: var(--c-yellow);
				}
			}
			
			svg {
				margin-right: rem(12);
			}

			span {
				font-size: rem(15);
				color: var(--c-text-gray);
			}

			@include min540 {
				margin-right: 0;
				// flex-wrap: wrap;

				&:first-child {
					flex: 0 0 50%;
					max-width: 50%;
					display: flex;
					justify-content: flex-start;

					order: 1;
				}

				&:nth-child(2) {
					flex: 0 0 100%;
					max-width: 100%;
					display: flex;
					justify-content: center;

					order: 3;
					margin-top: rem(20);
				}

				&:last-child {
					flex: 0 0 50%;
					max-width: 50%;
					display: flex;
					justify-content: flex-end;
				
					order: 2;
				}
			}
			
		}
	}
}

.balans_control {
	flex: 0 0 rem(412);
	max-width: rem(412);
	border-radius: rem(10);
	background-color: #18172A;

	@include min1200 {
		flex: 0 0 rem(305);
		max-width: rem(305);
	}

	@include min {
		flex: 0 0 100%;
		max-width: 100%;
		position: absolute;
		top: rem(-195);
		left: 0;
		width: 100%;
	}

	&__top {
		height: rem(116);
		display: flex;
		align-items: center;
		justify-content: center;

		.text {
			display: flex;
			align-items: center;

			span {
				margin-right: rem(13);
				font-weight: bold;
				font-size: rem(35);
				color: var(--c-text-gray-light);
			}
		}
	}

	&__bottom {
		display: flex;

		.button {
			border: rem(1.5) solid var(--c-yellow);
			height: rem(54);

			flex: 1;

			display: flex;
			align-items: center;
			justify-content: center;

			svg {
				path {
					fill: var(--c-text-gray-light);
				}

				@include min1200 {
					width: rem(12) !important;
					height: rem(12) !important;
				}
			}

			span {
				color: var(--c-text-gray-light);
				margin-left: rem(4);

				@include min1200 {
					font-size: rem(15);
				}
			}

			&:hover {
				background-color: var(--c-yellow);

				span {
					color: var(--c-text-dark);
				}

				svg {
					path {
						fill: var(--c-text-dark);
					}
				}

				&:last-child {
					svg {
						path {
							stroke: var(--c-yellow) !important;
						}
					}
				}
			}

			&:first-child {
				border-bottom-left-radius: var(--ui-border-4);

				@include min1200 {
					border-bottom-left-radius: rem(10);
				}
			}

			&:last-child {
				border-bottom-right-radius: var(--ui-border-4);

				@include min1200 {
					border-bottom-right-radius: rem(10);
				}
			}
		}
	}
}

.user_lk {
	display: flex;
	// flex-wrap: wrap;
	align-items: center;

	&__avatar {
		flex: 0 0 rem(150);
		max-width: rem(150);
		border-radius: 50%;
		overflow: hidden;
		margin-right: rem(92);

		@include min1200 {
			margin-right: rem(38);
		}

		@include min540 {
			flex: 0 0 rem(70);
			max-width: rem(70);
			// width: rem(70);
			margin-right: rem(8);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__text {
		display: flex;
		flex-direction: column;

		.title {
			margin-bottom: rem(14);
			font-weight: bold;
			font-size: rem(25);
			color: white;

			@include min540 {
				font-size: rem(20);
				margin-bottom: rem(5);
				max-width: rem(190);
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
		}

		.review {
			display: flex;
			flex-direction: column;
			margin-bottom: rem(10);

			@include min540 {
				margin-bottom: rem(5);
				flex-direction: row;
			}

			.flex {
				margin-bottom: rem(4);

				@include min540 {
					margin-bottom: 0;
					margin-right: rem(3);
				}

				svg {
					margin-right: rem(6);

					@include min540 {
						margin-right: rem(3);
						width: rem(18) !important;
						height: rem(18) !important;
					}

					&:last-child {
						margin-right: 0;
					}
				}
			}

			span {
				color: var(--c-text-gray);
				margin-top: rem(3);

				@include min540 {
					font-size: rem(13);
				}
			}
		}

		.data {
			font-size: rem(15);
			color: var(--c-text-gray);
			display: flex;

			.mobile {
				display: none;

				@include min540 {
					display: flex;
				}
			}

			.desktop {
				@include min540 {
					display: none;
				}
			}

			@include min540 {
				font-size: rem(13);
			}
		}
	}
}