.page_message_user {
	height: 100vh;
	overflow: hidden;
	background-color: #242336;

	@include min1280 {
		background-color: #18172B;
	}
}

.main_chat {
	display: flex;
	flex-wrap: wrap;
	height: 100vh;

	&__dialogs {
		flex: 0 0 rem(750);
		max-width: rem(750);
		padding: rem(34) rem(20) 0 rem(45);

		display: flex;
		flex-direction: column;

		height: calc(100% - 78px);

		// @include min1320 {
		// 	flex: 0 0 rem(550);
		// 	max-width: rem(550);
		// }

		@include min1280 {
			flex: 0 0 100%;
			max-width: 100%;
			height: 100%;
			overflow-x: hidden;
		}

		@include min {
			padding: rem(32) 0 0 0;
			// height: 100%;
		}

		.simplebar-scrollbar:before {
			opacity: 1 !important;
			background-color: var(--c-text-gray-light);
		}

		.field {
			margin-bottom: rem(30);
			flex: 0 0 rem(54);
			border: 0;
			background-color: #34334A;

			@include min {
				margin-left: 15px;
				margin-right: 15px;
				width: calc(100% - 30px);
			}
		}

		.dialogs_box {
			display: flex;
			flex-direction: column;
			flex: 1;
			// height: 100%;
			// overflow: auto;
			height: 100%;
			// padding-bottom: rem(120);

			@include min {
				height: unset;
			}

			.overflow {
				// height: 100%;
				// overflow-y: auto;
			}

			.item {
				cursor: pointer;
				height: rem(110);
				display: flex;
				align-items: center;
				padding: 0 rem(20);
				transition: var(--transition);
				margin-right: rem(35);
				border-radius: var(--ui-border-4);

				@include min1280 {
					background-color: #242336;
					margin-right: 0;
				}

				@include min540 {
					position: relative;

					&::before {
						content: "";
						position: absolute;
						bottom: 0;
						right: 15px;
						width: calc(100% - 110px);
						height: rem(1);
						background-color: #333238;
					}
				}

				&:hover {
					background-color: var(--c-bg);
				}

				&__image {
					flex: 0 0 rem(70);
					max-width: rem(70);
					height: rem(70);
					border-radius: 50%;
					margin-right: rem(27);
					overflow: hidden;

					@include min {
						flex: 0 0 rem(60);
						max-width: rem(60);
						height: rem(60);
						margin-right: rem(18);
					}

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}

				&__text {
					display: flex;
					// flex-direction: column;
					// width: 100%;
					flex: 1;
					min-width: 0;
					max-width: 100%;

					@include min1280 {
						max-width: 89%;
					}

					

					.left {
						// flex: 0 0 80%;
						// width: 80%;
						max-width: 100%;

						@include min1280 {
							// flex: 1;
							width: 95%;
							max-width: 95%;
							margin-right: rem(12);

							display: flex;
							flex-direction: column;
						}

						.title {
							white-space: nowrap;
							overflow: hidden;
							text-overflow: ellipsis;
							font-weight: 500;
							color: var(--c-text-gray-light);
							// margin-right: rem(30);
							
							margin-bottom: rem(11);

							@include max1280 {
								max-width: rem(395);
							}

							@include min540 {
								font-size: rem(15);
							}

							// @include min1280 {
							// 	max-width: 80%;
							// }
						}

						p {
							font-size: rem(15);
							color: var(--c-text-gray);

							max-width: rem(486);

							overflow: hidden;
							white-space: nowrap;
							text-overflow: ellipsis;
							margin-right: rem(-30);

							@include min540 {
								margin-right: rem(-70);
								max-width: 100%;
								font-size: rem(13);

								// &:after {
								// 	// content: "...";
								// 	display: inline-block;
								// 	position: absolute;
								// }

								// display: block;
								height: rem(36);
								position: relative;
								overflow: unset;
								white-space: unset;
								// text-overflow: unset;
							}

							span {
								color: var(--c-yellow);
							}
						}
						
					}
					.right {
						flex: 0 0 20%;
						max-width: 20%;

						@include min1280 {
							text-align: right;
							margin-left: auto;
							flex: 0 0 auto;
							max-width: 100%;
						}

						.date {
							display: flex;
							align-items: center;

							// flex: 0 0 rem(100);
							// max-width: rem(100);
							// width: rem(100);

							

							span {
								font-size: rem(14);
								color: var(--c-text-gray);
								margin-left: rem(13);
							}
						}
					}
				}

				&__date {
					align-self: flex-start;
					margin-top: rem(28);

					@include min540 {
						margin-top: rem(20);
					}

					.date {
							display: flex;
							align-items: center;

							// flex: 0 0 rem(100);
							// max-width: rem(100);
							// width: rem(100);

							@include min540 {
								flex-direction: column;
								align-items: flex-end;

								svg {
									width: rem(15) !important;
									height: rem(10) !important;
								}
							}

							span {
								font-size: rem(14);
								color: var(--c-text-gray);
								margin-left: rem(13);

								@include min540 {
									margin-top: rem(3);
									margin-left: 0;
									font-size: rem(10);
								}
							}
						}
				}
			}
		}
	}

	// &__dialogs {
	// 	position: relative;
	// 	height: 100%;

	// 	&::before {
	// 		content: "";
	// 		position: absolute;
	// 		top: 0;
	// 		left: 0;
	// 		width: 100%;
	// 		height: 100%;
	// 		backdrop-filter: blur(5px);
	// 		z-index: 10;
	// 	}
	// }

	&__messages {
		position: relative;
		display: flex;
		flex-direction: column;

		flex: 1;

		@include min1280 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 2;
			background-color: #18172B;

			transform: translateX(-100%);
			transition: var(--transition);
		}

		&_header {
			display: flex;
			align-items: center;
			justify-content: space-between;

			margin-top: rem(31);
			margin-bottom: rem(34);

			padding-left: rem(75);
			padding-right: rem(60);

			@include min1280 {
				margin-top: 0;
				margin-bottom: 0;

				padding: rem(22) rem(80) rem(22) rem(80);
				background-color: #242336;
			}

			@include min540 {
				padding: rem(14) rem(15) rem(14) rem(15);
			}

			.prev {
				display: none;

				@include min1280 {
					position: relative;
					display: block;

					span {
						position: absolute;
						top: rem(-7);
						right: rem(2);
						font-size: rem(13);
						color: var(--c-yellow);
					}
				}
			}

			.left {
				display: flex;
				flex-direction: column;

				@include min1280 {
					margin-right: auto;
					margin-left: rem(42);
				}

				@include min540 {
					margin-left: rem(18);
				}

				.title {
					margin-bottom: rem(10);
					font-weight: 500;
					color: white;

					@include min540 {
						margin-bottom: rem(6);
						font-size: rem(15);
					}
				}

				.date {
					font-size: rem(15);
					color: var(--c-text-gray);

					@include min540 {
						font-size: rem(13);
					}
				}
			}

			.right {
				@include min540 {
					.tb_box {
						height: rem(40);
						min-width: rem(40);
					}
				}
			}
		}

		&_wrap {
			background-color: #18172A;
			padding: rem(60) rem(34) rem(30) rem(80);

			display: flex;
			flex-direction: column;
			justify-content: flex-end;
			// height: 100%;
			flex: 1;

			@include min1440 {
				padding: rem(40) rem(35) rem(30) rem(35);
			}

			@include min540 {
				padding: rem(32) rem(15) rem(20) rem(15);
			}

			.message_right, .message_left {
				position: relative;
				background-color: rgba(56, 54, 98, .5);
				border-radius: rem(3);
				padding: rem(15) rem(62) rem(15) rem(20);

				margin-bottom: rem(30);

				@include min540 {
					margin-bottom: rem(10);
					padding: rem(10) rem(50) rem(10) rem(15);
				}

				&:last-child {
					margin-bottom: 0;
				}

				.text {
					color: var(--c-text-gray-light);

					@include min540 {
						font-size: rem(13);
					}
				}
				
				.date {
					position: absolute;
					bottom: rem(8);
					right: rem(6);
					color: var(--c-text-gray);

					@include min540 {
						font-size: rem(12);
						bottom: rem(5);
						right: rem(4);
					}

				}
			}

			.message_right {
				align-self: flex-end;
				max-width: 80%;
			}

			.message_left {
				align-self: flex-start;
				max-width: 80%;
			}
		}

		&_control {
			// position: absolute;
			// left: 0;
			// bottom: rem(120);

			height: rem(90);
			width: 100%;
			display: flex;
			align-items: center;

			padding-left: rem(50);
			padding-right: rem(85);

			background-color: #242336;

			@include min540 {
				padding-left: 15px;
				padding-right: 15px;
				height: rem(70);
			}

			.attach {
				margin-right: rem(40);

				@include min540 {
					margin-right: rem(20);
				}

				&:hover {
					svg {
						path {
							fill: var(--c-yellow);
						}
					}
				}

				svg {
					path {
						fill: var(--c-text-gray);
					}
				}
			}

			.text {
				flex: 1;
				height: rem(48);
				background-color: transparent;
				color: var(--c-text-gray);
				padding-right: rem(30);

				@include min540 {
					height: rem(32);
					font-size: rem(13);
				}

				@include placeholder {
					color: var(--c-text-gray);
				}
			}

			.send {
				&:hover {
					transform: translateX(10px);
				}
			}
		}
	}

	&__messages.active {
		@include min1280 {
			transform: translateX(0);
		}
	}
	
}